import React from 'react';
import {ResponsiveBar} from '@nivo/bar';
import './ChartBar.css';

const defaultNames = ["Плавание", "Т1", "Вел", "Т2", "Бег", "Финиш"];

function timeStringToFloat(time: string) {
    let hoursMinutesSeconds = time.split(/[.:]/);
    let hours = 0;
    let minutes = 0;
    let seconds = 0;
    switch (hoursMinutesSeconds.length) {
        case 3:
            hours = hoursMinutesSeconds[0] ? parseInt(hoursMinutesSeconds[0], 10) : 0;
            minutes = hoursMinutesSeconds[1] ? parseInt(hoursMinutesSeconds[1], 10) : 0;
            seconds = hoursMinutesSeconds[2] ? parseInt(hoursMinutesSeconds[2], 10) : 0;
            break;
        case 2:
            minutes = hoursMinutesSeconds[0] ? parseInt(hoursMinutesSeconds[0], 10) : 0;
            seconds = hoursMinutesSeconds[1] ? parseInt(hoursMinutesSeconds[1], 10) : 0;
            break;
        case 1:
            seconds = hoursMinutesSeconds[0] ? parseInt(hoursMinutesSeconds[0], 10) : 0;
            break;
    }
    return hours * 3600 + minutes * 60 + seconds;
}

function formatDuration(seconds: number) {
    return new Date(seconds * 1000).toISOString().substring(11, 11 + 8);
}

const ChartBar = (data: any) => {
    const mData = data.location.state;

    let timesData: any[] = [];

    mData.Times.forEach((value: any, index: number, array: any[]) => {
        let obj = {
            "RaceName": mData.RaceName,
            "Name": mData.Names ? mData.Names[index] : defaultNames[index],
            "Time": timeStringToFloat(mData.Times[index])
        };
        timesData.push(obj);
    });

    return (
        <>
            <text className={"title"}>{mData.Racer}<br/>{mData.RaceName}<br/>{mData.Date}</text>
            <ResponsiveBar
                data={timesData}
                keys={[
                    'Time'
                ]}
                indexBy="Name"
                margin={{
                    top: 100,
                    right: 100,
                    bottom: 50,
                    left: 100
                }}
                padding={0.3}
                valueScale={{type: 'linear'}}
                indexScale={{type: 'band', round: true}}
                valueFormat={value => formatDuration(value)}
                colors={{scheme: 'nivo'}}
                colorBy={'indexValue'}
                borderColor={{
                    from: 'color',
                    modifiers: [
                        [
                            'darker',
                            1.6
                        ]
                    ]
                }}
                axisTop={null}
                axisRight={null}
                axisBottom={{
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legendPosition: 'middle',
                    legendOffset: 32
                }}
                axisLeft={{
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legendPosition: 'middle',
                    legendOffset: -40,
                    format: value => formatDuration(value)
                }}
                labelSkipWidth={12}
                labelSkipHeight={12}
                labelTextColor={{
                    from: 'color',
                    modifiers: [
                        [
                            'darker',
                            1.6
                        ]
                    ]
                }}
                legends={[
                    {
                        dataFrom: 'indexes',
                        anchor: 'bottom-right',
                        direction: 'column',
                        justify: false,
                        translateX: 120,
                        translateY: 0,
                        itemsSpacing: 2,
                        itemWidth: 100,
                        itemHeight: 20,
                        itemDirection: 'left-to-right',
                        itemOpacity: 0.85,
                        symbolSize: 20,
                        effects: [
                            {
                                on: 'hover',
                                style: {
                                    itemOpacity: 1
                                }
                            }
                        ]
                    }
                ]}
                role="application"
            />
        </>
    )
}

export default ChartBar;