class TimeInterval {
    constructor(duration, effort, effort2, type) {
        let factor = 100;
        if (type === "erg") factor = factor * 2;
        this.duration = duration;
        this.effort = Math.round(effort * factor);
        this.effort2 = Math.round(effort2 * factor);
        this.start = 0;
    }

    get end() {
        return this.start + this.duration;
    }

    format_sec(s) {
        return ((s - (s %= 60)) / 60 + (9 < s ? '.' : '.0') + Math.round(s * 1.666667)).padEnd(8);
    }

    toString() {
        if (!this.effort2)
            return `${this.format_sec(this.start)}  ${this.effort}\n${this.format_sec(this.end)}  ${this.effort}\n`;
        else
            return `${this.format_sec(this.start)}  ${this.effort}\n${this.format_sec(this.end)}  ${this.effort2}\n`;
    }
}
export default TimeInterval;