import React, {Component} from "react";
import Chart from "react-apexcharts";
import './ChartBar.css';

class ChartApex extends Component {
    // @ts-ignore
    constructor(props) {
        super(props);

        this.state = {
            options: {
                chart: {
                    id: "basic-line"
                },
                xaxis: {
                    categories: [2014, 2015, 2016, 2017, 2018, 2019, 2020, 2021]
                }
            },
            series: [
                {
                    name: "Плавание",
                    data: ["24:48", "26:05", "29:07", "30:27", "32:35", "30:13", "14:19", "30:48"]
                }
            ]
        };
    }

    render() {
        return (
            <>
                <text className={"title"}>{"Sergey MIKHALEV"}<br/>{"2014-2021"}<br/>{"Плавание"}</text>
                <div className="row" style={{marginTop: "5%"}}>
                    <div className="mixed-chart" style={{marginLeft: 50}}>
                        <Chart
                            // @ts-ignore
                            options={this.state.options}
                            // @ts-ignore
                            series={this.state.series}
                            type="line"
                            height={500}
                        />
                    </div>
                </div>
            </>
        );
    }
}

export default ChartApex;