import Builder from 'xmlbuilder'
import {Bar, DurationType, Instruction, SportType} from './Editor'
import timeInterval from "../../timeInterval";
import TimeInterval from "../../timeInterval";

interface Workout {
  author: string;
  name: string;
  description: string;
  sportType: SportType;
  durationType: DurationType;
  tags: string[];
  separateBars: Array<Array<Bar>>;
  separateInstructions: Array<Array<Instruction>>;
}

export default function createWorkoutXml({
                                             author,
                                             name,
                                             description,
                                             sportType,
                                             durationType,
                                             tags,
                                             separateBars,
                                             separateInstructions
                                         }: Workout, idx: number, separateAuthor: String, fileName: String, separateDescription: String, separateTag: String): string {
  var totalTime = 0
  var totalLength = 0

  let xml = Builder.begin()
      .ele('workout_file')
      .ele('author', separateAuthor).up()
      .ele('name', fileName).up()
      .ele('description', separateDescription).up()
      .ele('sportType', sportType).up()
      .ele('durationType', durationType).up()
      .ele('tags', separateTag)

  tags.forEach((tag: string) => {
    var t: Builder.XMLNode
    t = Builder.create('tag')
        .att('name', tag)
    xml.importDocument(t)
  })

  xml = xml.up().ele('workout')
  separateBars[idx].forEach((bar, index) => {
      var segment: Builder.XMLNode
      var ramp

      if (bar.type === 'bar') {
        segment = Builder.create('SteadyState')
            .att('Duration', durationType === 'time' ? bar.time : bar.length)
            .att('Power', bar.power)
            .att('pace', bar.pace)

        // add cadence if not zero
        bar.cadence !== 0 && segment.att('Cadence', bar.cadence)

      } else if (bar.type === 'trapeze' && bar.startPower && bar.endPower) {

        // index 0 is warmup
        // last index is cooldown
        // everything else is ramp

        ramp = 'Ramp'
        if (index === 0) ramp = 'Warmup'
        if (index === separateBars.length - 1) ramp = 'Cooldown'

        if (bar.startPower < bar.endPower) {
          // warmup
          segment = Builder.create(ramp)
              .att('Duration', durationType === 'time' ? bar.time : bar.length)
              .att('PowerLow', bar.startPower)
              .att('PowerHigh', bar.endPower)
              .att('pace', bar.pace)
          // add cadence if not zero
          bar.cadence !== 0 && segment.att('Cadence', bar.cadence)

        } else {
          // cooldown
          segment = Builder.create(ramp)
              .att('Duration', durationType === 'time' ? bar.time : bar.length)
              .att('PowerLow', bar.startPower) // these 2 values are inverted
              .att('PowerHigh', bar.endPower) // looks like a bug on zwift editor
              .att('pace', bar.pace)
          // add cadence if not zero
          bar.cadence !== 0 && segment.att('Cadence', bar.cadence)
        }
      } else if (bar.type === 'interval') {
        // <IntervalsT Repeat="5" OnDuration="60" OffDuration="300" OnPower="0.8844353" OffPower="0.51775455" pace="0"/>
        segment = Builder.create('IntervalsT')
            .att('Repeat', bar.repeat)
            .att('OnDuration', durationType === 'time' ? bar.onDuration : bar.onLength)
            .att('OffDuration', durationType === 'time' ? bar.offDuration : bar.offLength)
            .att('OnPower', bar.onPower)
            .att('OffPower', bar.offPower)
            .att('pace', bar.pace)
        // add cadence if not zero
        bar.cadence !== 0 && segment.att('Cadence', bar.cadence)
        // add cadence resting if not zero
        bar.restingCadence !== 0 && segment.att('CadenceResting', bar.restingCadence)
      } else {
        // free ride
        segment = Builder.create('FreeRide')
            .att('Duration', durationType === 'time' ? bar.time : bar.length)
            .att('FlatRoad', 0) // Not sure what this is for
        // add cadence if not zero
        bar.cadence !== 0 && segment.att('Cadence', bar.cadence)
      }

      // add instructions if present
      if (durationType === 'time') {
        separateInstructions[idx].filter((instruction) => (instruction.time >= totalTime && instruction.time < (totalTime + bar.time))).forEach((i) => {
          segment.ele('textevent', { timeoffset: (i.time - totalTime), message: i.text })
        })
      } else {
        separateInstructions[idx].filter((instruction) => (instruction.length >= totalLength && instruction.length < (totalLength + (bar.length || 0)))).forEach((i) => {
          segment.ele('textevent', { distoffset: (i.length - totalLength), message: i.text })
        })
      }

      xml.importDocument(segment)

      totalTime = totalTime + bar.time
      totalLength = totalLength + (bar.length || 0)
    });

  return xml.end({ pretty: true });
}

export function createWorkoutMrcErg({ author, name, description, sportType, durationType, tags, separateBars, separateInstructions }: Workout, idx: number, fileName: String, type: string): string {

    let intervals: timeInterval[] = [];

    separateBars[idx].forEach((bar, index) => {
        if (bar.type === 'bar') {
            let interval = new TimeInterval(bar.time, bar.power, null, type);
            intervals.push(interval);
        } else if (bar.type === 'trapeze' && bar.startPower && bar.endPower) {
            let interval = new TimeInterval(bar.time, bar.startPower, bar.endPower, type);
            intervals.push(interval);
        }
    });

    intervals.forEach((cur, i) => {
        const prev = intervals[i - 1];
        cur.start = (!prev || i == 0)
            ? 0
            : prev.end;
    });


    let out = "[COURSE HEADER]\nUNITS = ENGLISH\nVERSION = 2\n";
    out += `FILE NAME = ${fileName}.${type}\n`;
    if (type == 'erg')
        out += `FTP = 200\n`;
    out += `MINUTES ${(type == 'mrc') ? 'PERCENT' : 'WATTS'}\n`;
    out += "[END COURSE HEADER]\n";
    out += "[COURSE DATA]\n";
    out += intervals.map(s => s.toString()).join('');
    out += "[END COURSE DATA]"
    return out;
}